import * as S from "./style";
import { DodamFilledButton, Photo } from "@b1nd/dds-web";
import { useState } from "react";
import { useUploadMutation } from "../../../queries/Upload/upload.query";

interface PosterProps {
  title: string;
  onSubmit: (summary: string, image: string | null) => void; 
  onBack: () => void;
}

const Poster = ({ title, onSubmit, onBack }: PosterProps) => {
  const [summary, setSummary] = useState("");
  const [image, setImage] = useState<File | null>(null);
  const uploadMutation = useUploadMutation();

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) setImage(file);
  };

  const handleSubmit = async () => {
    if (!summary.trim()) return alert(" .");

    let imageUrl: string | null = null;

    if (image) {
      try {
        const data = await uploadMutation.mutateAsync({ file: image });
         imageUrl = data.url; 
      } catch (error) {
        console.error(error);
        alert("   .");
        return;
      }
    }

    onSubmit(summary, imageUrl); 
  };

  return (
    <S.PosterContainer>
      <S.PosterBox>
        <h1> </h1>
     
      <S.PosterImage>
        {image ? <img src={URL.createObjectURL(image)} alt="" width={200} />:
        
        (
          <>
          <Photo size={128} color="labelDisabled"/>
        <S.ImgButton htmlFor="poster-upload"> </S.ImgButton>
        <input id="poster-upload" type="file" placeholder=" "  onChange={handleImageChange} />
          </>
        )}  
      </S.PosterImage>
      <span>{title}</span>
      <S.SummaryInput
        placeholder=" "
        value={summary}
        onChange={(e) => setSummary(e.target.value)}
      />
      <S.PosterButtonBox>
      <DodamFilledButton
        width={112}
        size="Large"
        backgroundColorType="Secondary"
        textTheme="staticWhite"
        onClick={onBack}
      >
        
      </DodamFilledButton>
      
      <DodamFilledButton
        width={130}
        size="Large"
        backgroundColorType="Primary"
        textTheme="staticWhite"
        onClick={handleSubmit}
      >
         
      </DodamFilledButton>
  
      </S.PosterButtonBox>
      </S.PosterBox>
 
    </S.PosterContainer>
  );
};

export default Poster;
