import {
  BlogBox,
  BlogContentBox,
  BlogContentContainer,
  PagiNation,
  ToastText,
} from './style';
import { Title } from '../style';
import { DodamErrorBoundary } from '@b1nd/dds-web';
import BlogItem from '../../components/Blogitem';
import WriteButton from '../../components/WriteButton';
import { useState } from 'react';
import { useBlogsList } from '../../queries/Blog/blog.query';

const Blog = () => {
  const [page, setPage] = useState(1);
  const { data, isLoading, isFetching } = useBlogsList({ page });

  const totalPages = data?.totalPage || 1;

  const handlePrev = () => {
    if (page > 1) setPage(prev => prev - 1);
  };

  const handleNext = () => {
    if (page < totalPages) setPage(prev => prev + 1);
  };


  return (
    <BlogBox>
      <Title>
        <h1>
            
          <br />
           
        </h1>
        <span>
                .
          <br />
                  .
        </span>
      </Title>

      <div style={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
        <BlogContentContainer>
          {/* <BlogSerach>
            <BlogSerachIcon>
              <Magnifyingglass color='labelAlternative' />
            </BlogSerachIcon>
            <BlogSerachInput placeholder='' />
          </BlogSerach> */}

          <BlogContentBox>
            <DodamErrorBoundary text="" showButton={true}>
            {isLoading || isFetching ? (
              <ToastText>..</ToastText>
            ) : data?.data.length === 0 ? (
              <ToastText>
                 .
              </ToastText>
            ) : (
              <>
                {data?.data.map((item, index) => (
                  <BlogItem key={index} data={item} />
                ))}
              </>
            )}
            </DodamErrorBoundary>
          </BlogContentBox>

          
          <PagiNation>
            <button disabled={page === 1} onClick={handlePrev}>
              
            </button>
            <span>
              {page} / {totalPages}
            </span>
            <button disabled={page === totalPages} onClick={handleNext}>
              
            </button>
          </PagiNation>
        </BlogContentContainer>
      </div>

      <WriteButton />
    </BlogBox>
  );
};

export default Blog;
